import React, { useEffect, useState } from "react";
import "./style.css";
import HeaderAdm from "../../../components/HeaderAdm";
import axios from "axios";
import { BASE_URL } from "../../../config/dev";
import Header from "../../../components/HeaderAdm";
import Footer from "../../../components/Footer";

const HomeResp = props => {
  const [ni, setNi] = useState(window.localStorage.getItem("nivel"));
  const [idUser, setIdUser] = useState(window.localStorage.getItem("cod"));
  const [listUser, setListUser] = useState([]);

  useEffect(() => {
    const nivel = window.localStorage.getItem("nivel");
    // if (nivel != 1 && nivel != 0) {
    //   window.location.href = "/";
    // }
    setNi(nivel);
    getUser();
    // console.log("json2");
    // console.log(listUser);
  }, []);

  const getUser = () => {
    axios({
      method: "get",
      url: `${BASE_URL}pais/getPaisById.php?cod=${idUser}`
    })
      .then(function(response) {
        let data = response.data;
        let str = data.split("next,");
        let json = [];
        // console.log(str);

        for (let i = 0; i < str.length - 1; i++) {
          let registro = str[i].split(",");
          json.push({
            id: registro[0],
            cpf: registro[1],
            senha: registro[2],
            nome_resp1: registro[3],
            nome_resp2: registro[4],
            tel1: registro[5],
            tel2: registro[6],
            matricula_aluno: registro[7],
            nome_aluno: registro[8],
            turma_aluno: registro[9],
            ano_letivo_aluno: registro[10],
            nivel: registro[11]
          });
        }
        // console.log(json);
        setListUser(json[0]);

        // console.log("list");
        // console.log(list);
      })
      .catch(function(response) {
        // console.log(response);
      });
  };

  const infoAluno = () => {
    // console.log("listUser[0].nome_resp1");
    // console.log(listUser.nome_resp1);
    return (
      <div className="container-info-aluno">
        <h2 className="margin-top-adm">Bem vindo {listUser.nome_resp1}</h2>
        <h4 className="title-perfil-aluno">Perfil do aluno:</h4>
        <ul className="list-dados-aluno">
          <li className="item-dados-aluno">Aluno: {listUser.nome_aluno}</li>
          <li className="item-dados-aluno">
            Matrícula: {listUser.matricula_aluno}
          </li>
          <li className="item-dados-aluno">Turma: {listUser.turma_aluno}</li>
          <li className="item-dados-aluno">
            Ano letivo: {listUser.ano_letivo_aluno}
          </li>
        </ul>
      </div>
    );
  };

  const renderNivelAccess = () => {
    let retorno = null;
    if (props.nivel == 0) {
      retorno = (
        <>
          <section className="home-resp">
            <Header nivel={props.nivel} userName={props.userName} />

            {infoAluno()}
          </section>
        </>
      );
    } else {
      retorno = <div></div>;
    }
    return retorno;
  };

  return renderNivelAccess();
};

export default HomeResp;
