import React, { Component } from "react";
import CKEditor from "@ckeditor/ckeditor5-react";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import axios from "axios";
import "./style.css";
import { BASE_URL } from "../../../config/dev";
import Header from "../../../components/HeaderAdm";
// import Footer from "../../../components/Footer";

class Mural extends Component {
  constructor(props) {
    super(props);
    this.state = { text: "" };
  }

  componentDidMount() {
    // console.log("this.state");
    // console.log(this.state);
    // if (this.props.nivel != 1 && this.props.nivel != 0) {
    //   window.location.href = "/";
    // }
    this.textoAtual();
  }

  textoAtual() {
    axios({
      method: "get",
      url: `${BASE_URL}mural/getMural.php`
    }).then(response => {
      // console.log("response text get");
      // console.log(response.data);
      this.setState({
        text: response.data
      });
    });
  }

  renderNivelAccess() {
    let retorno = null;
    if (this.props.nivel == 1) {
      retorno = (
        <form method="post" action={`${BASE_URL}mural/postMural.php`}>
          <div className="container container-mural-editor">
            <CKEditor
              editor={ClassicEditor}
              onInit={editor => {
                // You can store the "editor" and use when it is needed.
                // console.log("Editor is ready to use!", editor);
              }}
              onChange={(event, editor) => {
                const data = editor.getData();
                // console.log("onchange event");
                // console.log({ event, editor, data });
                this.state.text = data;
                // console.log(this.state.text);
                document.getElementById("mural_text").value = data;
              }}
              onBlur={(event, editor) => {
                // console.log("Blur.", editor);
              }}
              onFocus={(event, editor) => {
                // console.log("Focus.", editor);
              }}
            />
          </div>
          <input name="mural_text" id="mural_text" type="hidden" />
          <div className="center">
            <button
              type="submit"
              className="btn waves-effect waves-light green"
            >
              Salvar
            </button>
          </div>
        </form>
      );
    } else {
      retorno = <div></div>;
    }
    return retorno;
  }

  renderUserAutenticate() {
    let retorno = null;
    if (this.props.nivel == 1 || this.props.nivel == 0) {
      retorno = (
        <>
          <Header nivel={this.props.nivel} userName={this.props.userName} />
          <div id="mural-adm">
            <h2 className="margin-top-adm">Mural</h2>
            {this.renderNivelAccess()}

            <br />
            <div className="container container-view-mural">
              <div className="center">
                <h4>mural visualização:</h4>
              </div>
              <div dangerouslySetInnerHTML={{ __html: this.state.text }} />
            </div>
          </div>
        </>
      );
    }
    return retorno;
  }

  render() {
    return this.renderUserAutenticate();
  }
}

export default Mural;

