import React, { Component } from "react";
import M from "materialize-css";
import "./style.css";
import "materialize-css/dist/css/materialize.min.css";
import { BASE_URL } from "../../config/dev";
import CKEditor from "@ckeditor/ckeditor5-react";
import ClassicEditor from "@ckeditor/ckeditor5-build-classic";
import axios from "axios";

class ModalDetalhesResp extends Component {
  constructor(props) {
    super(props);
    this.state = {
      textComunicado: "",
      listComunicado: {}
    };
  }

  async componentDidMount() {
    await this.getComunicado();
    const options = {
      onOpenStart: () => {
        // console.log("Open Start");
      },
      onOpenEnd: () => {
        // console.log("Open End");
      },
      onCloseStart: () => {
        // console.log("Close Start");
      },
      onCloseEnd: () => {
        // console.log("Close End");
      },
      inDuration: 250,
      outDuration: 250,
      opacity: 0.5,
      dismissible: false,
      startingTop: "4%",
      endingTop: "10%"
    };
    M.Modal.init(this.Modal, options);
    // console.log("state com json convertido");
    // console.log(this.state.listComunicado);
  }

  getComunicado() {
    axios({
      method: "get",
      url: `${BASE_URL}comunicados/getComunicados.php?cod=${this.props.details.id}`
    }).then(response => {
      // console.log("response text get comunicados");
      // console.log(response);
      let data = response.data;
      let str = data.split("next,");
      let json = [];
      for (let i = 0; i < str.length - 1; i++) {
        let registro = str[i].split(",");
        // teste caso não seja inserido img
        if (registro[2] == "") {
          registro[5] = registro[4];
          registro[4] = registro[3];
          registro[3] = "";
        }
        json.push({
          id: registro[0],
          id_user: registro[1],
          typeImg: registro[2],
          img: registro[3],
          text: registro[4],
          createdAt: registro[5]
        });
      }
      // console.log("testessss");
      // console.log(json);
      this.setState({
        listComunicado: json
      });
      // console.log("testessss 33333");
      // console.log(this.state.listComunicado);
    });
  }

  exibeImgComunicado(type, img) {
    let retorno = null;
    if (img != "") {
      retorno = (
        <div className="center container-img-comunicados">
          <img
            className="img-comunicados responsive-img"
            src={`${type},${img}`}
          />
        </div>
      );
    } else {
      retorno = <div></div>;
    }
    return retorno;
  }

  renderizaComunicado() {
    // console.log("renderiza comunicado");
    // console.log(this.state.listComunicado);
    const itens = this.state.listComunicado;
    // console.log(itens);
    let rows = [];
    for (let i = 0; i < itens.length; i++) {
      rows.push(
        <div className="card-comunicado" key={i}>
          <div>
            <b>Comunicado: {itens[i].createdAt}</b>
          </div>
          <div dangerouslySetInnerHTML={{ __html: itens[i].text }} />
          {this.exibeImgComunicado(itens[i].typeImg, itens[i].img)}
          <br />
        </div>
      );
    }
    return rows;
  }

  convertBase64(e, element, id) {
    var filesSelected = document.getElementById(element).files;
    if (filesSelected.length > 0) {
      var fileToLoad = filesSelected[0];
      var fileReader = new FileReader();

      fileReader.onload = function(fileLoadedEvent) {
        var srcData = fileLoadedEvent.target.result; // <--- data: base64
        // console.log("srcData");
        // console.log(srcData);
        document.getElementById(`mural_img_comunicado_${id}`).value = srcData;
        // rgBase64 = srcData;
        // document.getElementById("rgCandidatoHidden").value = srcData;
      };
      fileReader.readAsDataURL(fileToLoad);
    }
  }

  getDate() {
    const date = new Date();
    const dia = `00${date.getDate()}`;
    const mes = `00${date.getMonth() + 1}`;
    const ano = date.getFullYear();
    const fullDate = `${dia.slice(-2)}/${mes.slice(-2)}/${ano}`;
    return fullDate;
  }

  render() {
    return (
      <>
        <a
          title="COMUNICADO"
          className="waves-effect waves-light btn modal-trigger green btn-margin-left"
          data-target={`modal_resp_comunicado_${this.props.details.id}`}
        >
          <i class="material-icons">import_contacts</i>
        </a>

        <div
          ref={Modal => {
            this.Modal = Modal;
          }}
          id={`modal_resp_comunicado_${this.props.details.id}`}
          className="modal"
        >
          <form
            className="row"
            method="post"
            action={`${BASE_URL}comunicados/postComunicados.php`}
          >
            <div className="modal-content">
              <h4>Comunicado do responsável {this.props.details.nome_resp1}</h4>
              {/* <div>id: {this.props.details.id}</div> */}
              <CKEditor
                editor={ClassicEditor}
                onInit={editor => {
                  // You can store the "editor" and use when it is needed.
                  // console.log("Editor is ready to use!", editor);
                }}
                onChange={(event, editor) => {
                  const data = editor.getData();
                  // console.log("onchange event");
                  // console.log({ event, editor, data });
                  this.state.textComunicado = data;
                  // console.log(this.state.textComunicado);
                  document.getElementById(
                    `mural_text_comunicado_${this.props.details.id}`
                  ).value = data;
                }}
              />
              <input
                name="mural_text_comunicado"
                id={`mural_text_comunicado_${this.props.details.id}`}
                type="hidden"
              />
              <input
                name="mural_img_comunicado"
                id={`mural_img_comunicado_${this.props.details.id}`}
                type="hidden"
              />
              <input
                name="mural_id_user"
                id="mural_id_user"
                value={this.props.details.id}
                type="hidden"
              />
              <input
                name="createdAt"
                id="createdAt"
                value={this.getDate()}
                type="hidden"
              />

              <div class="file-field input-field">
                <div class="btn orange">
                  <span>FOTO</span>
                  <input
                    type="file"
                    className=""
                    id={`file_comunicado_${this.props.details.id}`}
                    onChange={e => {
                      this.convertBase64(
                        e,
                        `file_comunicado_${this.props.details.id}`,
                        this.props.details.id
                      );
                    }}
                    name="file_comunicado"
                  />
                </div>
                <div class="file-path-wrapper">
                  <input
                    className="file-path validate input-file"
                    type="text"
                    placeholder="SELECIONE..."
                  />
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <div className="modal-close waves-effect waves-red btn red mr-2">
                CANCELAR
              </div>
              <button
                type="submit"
                className="modal-close waves-effect waves-green btn green btn-confirm-comunicado"
              >
                CONFIRMAR
              </button>
            </div>
          </form>

          {this.renderizaComunicado()}
        </div>
      </>
    );
  }
}

export default ModalDetalhesResp;
