  constructor(props) {
    super(props);
    this.state = {
      listImg: [],
      attImg: false,
      respBruto: "",
      qtdImg: 0, // total img
      imgAtual: 1 // img exibida
    };
  }

  async componentDidMount() {
    // encerra sessão
    // localStorage.removeItem("nivel");
    // console.log("this.getImg()");
    // console.log(this.getImg());
    this.setState({
      listImg: await this.getImg()
    });
    // console.log("this.state.listImgg");
    // console.log(this.state.listImg);
    this.intervalId = setInterval(this.nextImg.bind(this), 4000);
  }

  componentWillUnmount() {
    clearInterval(this.intervalId);
  }

  imagensGaleria() {
    let rows = [];
    let itensImg = this.state.listImg;
    // console.log("itensImg");
    // console.log(itensImg);
    for (let i = 0; i < itensImg.length; i++) {
      rows.push(
        <div className="item-galeria" key={i}>
          <img
            src={`${BASE_URL}/galeria/${itensImg[i].diretorio}`}
            className="img-galeria"
            alt=""
          />
        </div>
      );
    }

    return rows;
  }
  async getImg() {
    const respImg = await axios({
      method: "get",
      url: `${BASE_URL}galeria/getImg.php`
    })
      .then(function(response) {
        // console.log("response.data");
        // console.log(response.data);
        let data = response.data;
        let str = data.split("next,");
        let json = [];
        for (let i = 0; i < str.length - 1; i++) {
          let registro = str[i].split(",");
          json.push({
            id: registro[0],
            diretorio: registro[1].slice(2, registro[1].length)
          });
        }
        // setListImg(json);
        // setAttImg(true);
        // console.log("resp img json");
        // console.log(json);
        // console.log(json.length);

        // this.setState({
        //   listImg: json,
        //   qtdImg: json.length
        // });
        return json;
      })
      .catch(function(response) {});

    return respImg;
  }

  autoPlayGaleria() {
    let atual = this.state.imgAtual;
    let qtd = this.state.qtdImg;
    setInterval(function(atual, qtd) {
      let arrImg = document.getElementsByClassName("item-galeria");
      arrImg[atual - 1].style.display = "none";
      if (atual == qtd) {
        atual = 1;
      } else {
        atual++;
      }
      this.setState({
        imgAtual: atual
      });
      arrImg[atual - 1].style.display = "block";
    }, 2000);
  }

  nextImg(event) {
    // console.log(this.state.imgAtual);
    // console.log("proximo");
    // console.log(event);
    let atual = this.state.imgAtual;
    // let qtd = this.state.qtdImg;
    let arrImg = document.getElementsByClassName("item-galeria");
    let qtd = arrImg.length;
    arrImg[atual - 1].style.display = "none";
    if (atual == qtd) {
      atual = 1;
    } else {
      atual++;
    }
    this.setState({
      imgAtual: atual
    });
    arrImg[atual - 1].style.display = "block";
  }
  previousImg(event) {
    // console.log("anterior");
    // console.log(event);
    let atual = this.state.imgAtual;
    // let qtd = this.state.qtdImg;
    let arrImg = document.getElementsByClassName("item-galeria");
    let qtd = arrImg.length;
    arrImg[atual - 1].style.display = "none";
    if (atual == 1) {
      atual = qtd;
    } else {
      atual--;
    }
    this.setState({
      imgAtual: atual
    });
    arrImg[atual - 1].style.display = "block";
  }

