import React, { useEffect, useState } from "react";
import "./style.css";
import { BASE_URL } from "../../../config/dev";
import Header from "../../../components/Header";
import Footer from "../../../components/Footer";

const Login = () => {
  const [msgError, setMsgError] = useState(false);

  useEffect(() => {
    // encerra sessão
    // localStorage.removeItem("nivel");
    let paramsHash = window.location.hash;
    let hashMsgError = paramsHash.slice(7);
    // console.log("paramsHash");
    // console.log(paramsHash);
    // console.log("hashMsgError");
    // console.log(hashMsgError);
    if (hashMsgError !== "") {
      setMsgError(true);
    }
  });

  const loginError = exibe => {
    let retorno = null;
    if (exibe) {
      retorno = (
        <div className="center msg-error-login">
          <small className="red-text">
            Nome de usuário ou senha inválidos!
          </small>
        </div>
      );
    } else {
      retorno = <div></div>;
    }
    return retorno;
  };

  return (
    <>
      <Header />
      <section id="responsaveis">
        <h2 className="white-text">área dos responsáveis</h2>
        <div className="container container-login-form">
          <form
            className="row"
            method="post"
            action={`${BASE_URL}login/autenticacao.php`}
          >
            <div className="input-field col s12 m12">
              <i class="material-icons prefix">account_circle</i>
              <input
                id="user_login"
                name="user_login"
                type="text"
                className="validate input-responsaveis"
                maxLength="60"
              />
              <label for="user_login">Usuário</label>
            </div>
            <div className="input-field col s12 m12">
              <i class="material-icons prefix">lock</i>
              <input
                id="user_password"
                name="user_password"
                type="password"
                className="validate input-responsaveis"
                maxLength="60"
              />
              <label for="user_password">Senha</label>
            </div>

            {loginError(msgError)}

            <div className="col s12 center">
              <button
                className="btn waves-effect waves-light center green"
                type="submit"
                name="action"
              >
                ENTRAR
                <i className="material-icons right">send</i>
              </button>
            </div>
          </form>
        </div>
      </section>
      <Footer />
    </>
  );
};

export default Login;
