import React, { Component } from "react";
import "./style.css";
import M from "materialize-css/dist/js/materialize.min.js";
import imgMiniLogo from "../../assets/mini_logo.png";
import { Link } from "react-router-dom";

// const Header = () => {
class Header extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    // if (this.props.nivel != 1 && this.props.nivel != 0) {
    //   window.location.href = "/";
    // }
    document.addEventListener("DOMContentLoaded", function() {
      var elems = document.querySelectorAll(".sidenav");
      var instances = M.Sidenav.init(elems, {});
    });
    console.log("this.props resps");
    console.log(this.props);
  }

  goNavigate(event, num) {
    let tamHeight = [];
    let tamFull = document.body.offsetHeight;
    // tamHeight.push(document.getElementById('responsaveis-adm').offsetHeight);
    // tamHeight.push(document.getElementById('cardapio-adm').offsetHeight);
    // tamHeight.push(document.getElementById('calendario-adm').offsetHeight);
    // tamHeight.push(document.getElementById('mural-adm').offsetHeight);
    // tamHeight.push(document.getElementById('galeria-adm').offsetHeight);
    if (this.props.nivel == 1) {
      tamHeight.push(document.getElementById("responsaveis-adm").offsetHeight);
    }
    tamHeight.push(document.getElementById("cardapio-adm").offsetHeight);
    tamHeight.push(document.getElementById("calendario-adm").offsetHeight);
    tamHeight.push(document.getElementById("mural-adm").offsetHeight);
    if (this.props.nivel == 1) {
      tamHeight.push(document.getElementById("galeria-adm").offsetHeight);
    }

    let position = 0;
    for (let i = 0; i < num; i++) {
      position += tamHeight[i];
    }
    window.scroll(0, position);
  }

  renderNivelAccess() {
    let retorno = null;
    if (this.props.nivel == 1) {
      retorno = (
        <li>
          <Link to="/galeriaAdm">GALERIA</Link>
        </li>
      );
    } else {
      retorno = <li></li>;
    }
    return retorno;
  }
  // renderNivelAccessResponsaveis() {
  //   let retorno = null;
  //   if (this.props.nivel == 1) {
  //     retorno = (
  //       <li>
  //         <Link to="/acesso">RESPONSÁVEIS</Link>
  //       </li>
  //     );
  //   } else {
  //     retorno = <div></div>;
  //   }
  //   return retorno;
  // }
  renderNivelAccessMobile() {
    let retorno = null;
    if (this.props.nivel == 1) {
      retorno = (
        // <li>
        //   <a onClick={e => this.goNavigate(e, 1)}>GALERIA</a>
        // </li>
        <div></div>
      );
    } else {
      retorno = <div></div>;
    }
    return retorno;
  }
  // renderNivelAccessMobileResponsaveis() {
  //   let retorno = null;
  //   if (this.props.nivel == 1) {
  //     retorno = (
  //       <li>
  //         <Link to="/acesso">RESPONSÁVEIS</Link>
  //       </li>
  //     );
  //   } else {
  //     retorno = <div></div>;
  //   }
  //   return retorno;
  // }

  renderComunicadosResp() {
    let retorno = null;
    if (this.props.nivel == 0) {
      retorno = (
        <li>
          <Link to="/comunicados">COMUNICADOS</Link>
        </li>
      );
    } else {
      retorno = <li></li>;
    }
    return retorno;
  }
  render() {
    return (
      <div>
        <nav className="nav-extended container-header-adm header-position-adm">
          <div className="welcome-user">
            <div>Bem vindo {this.props.userName}!</div>
          </div>
          <div className="nav-wrapper">
            <a href="/" className="brand-logo">
              <img src={imgMiniLogo} className="mini-logo" alt="mini logo" />
            </a>
            <a href="/" data-target="mobile-demo" className="sidenav-trigger">
              <i className="material-icons">menu</i>
            </a>
            <ul id="nav-mobile" className="right hide-on-med-and-down">
              {/* {this.renderNivelAccessResponsaveis()} */}
              <li>
                <Link to="/acesso">RESPONSÁVEIS</Link>
              </li>

              <li>
                <Link to="/cardapio">CARDÁPIO</Link>
              </li>
              <li>
                <Link to="/calendario">CALENDÁRIO</Link>
              </li>
              <li>
                <Link to="/mural">MURAL</Link>
              </li>
              {this.renderComunicadosResp()}
              {this.renderNivelAccess()}
              <li>
                {/* <a href="/">SAIR</a> */}
                <a
                  onClick={e => {
                    // window.localStorage.removeItem("nivel");
                    window.localStorage.clear();
                  }}
                  href="/"
                >
                  SAIR
                </a>
              </li>

              {/*this.renderNivelAccess()*/}
            </ul>
          </div>
        </nav>

        <ul className="sidenav header-position-adm" id="mobile-demo">
          {/* {this.renderNivelAccessMobileResponsaveis()}
           */}
          <li>
            <Link to="/acesso">RESPONSÁVEIS</Link>
          </li>
          <li>
            <Link to="/cardapio">CARDÁPIO</Link>
          </li>
          <li>
            <Link to="/calendario">CALENDÁRIO</Link>
          </li>

          <li>
            <Link to="/mural">MURAL</Link>
          </li>

          {this.renderComunicadosResp()}
          {/* {this.renderNivelAccessMobile()} */}
          {this.renderNivelAccess()}
          <li>
            <a
              onClick={e => {
                // window.localStorage.removeItem("nivel");
                window.localStorage.clear();
              }}
              href="/"
            >
              SAIR
            </a>
          </li>
        </ul>
      </div>
    );
  }
}

export default Header;
