import React, { useEffect, useState } from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import HomeAdm from "./pages/adm/Home";
import * as serviceWorker from "./serviceWorker";
import "materialize-css/dist/css/materialize.min.css";
import Midleware from "./pages/midleware";
import { HashRouter, Switch, Route, Router, Link } from "react-router-dom";
import QuemSomos from "./pages/LandingPage/QuemSomos";
import Parcerias from "./pages/LandingPage/Parcerias";
import Atividades from "./pages/LandingPage/Atividades";
import Galeria from "./pages/LandingPage/Galeria";
import AreaResponsaveis from "./pages/LandingPage/Login";
import Localizacao from "./pages/LandingPage/Localizacao";

import Responsaveis from "./pages/adm/Responsaveis";
import ResponsaveisList from "./pages/adm/ResponsaveisList";
import Cardapio from "./pages/adm/Cardapio";
import Calendario from "./pages/adm/Calendario";
import Mural from "./pages/adm/Mural";
import Comunicados from "./pages/adm/ComunicadosResp";
import GaleriaAdm from "./pages/adm/Galeria";

import HomeResp from "./pages/adm/HomeResp";

// import Header from "./components/HeaderAdm";

const Rotas = () => {
  const [ni, setNi] = useState(window.localStorage.getItem("nivel"));
  const [nameUser, setNameUser] = useState(window.localStorage.getItem("user"));

  useEffect(() => {
    const nivel = window.localStorage.getItem("nivel");
    setNi(nivel);
    const name = window.localStorage.getItem("user");
    setNameUser(name);
  });
  return (
    <HashRouter>
      <Switch>
        {/* <Route path="/" exact={true} component={App} />
          <Route path="/acesso" component={HomeAdm} />
          <Route path="/midle" component={Midleware} /> */}
        <Route path="/" exact={true} component={QuemSomos} />
        <Route path="/parcerias" component={Parcerias} />
        <Route path="/atividades" component={Atividades} />
        <Route path="/galeria" component={Galeria} />
        <Route path="/login" component={AreaResponsaveis} />
        <Route path="/localizacao" component={Localizacao} />

        <Route
          path="/acesso"
          render={props => {
            return (
              <>
                <Responsaveis nivel={ni} userName={nameUser} />
                <HomeResp nivel={ni} userName={nameUser} />
                <ResponsaveisList nivel={ni} userName={nameUser} />
              </>
            );
          }}
        />
        <Route
          path="/cardapio"
          render={props => <Cardapio nivel={ni} userName={nameUser} />}
        />
        <Route
          path="/calendario"
          render={props => <Calendario nivel={ni} userName={nameUser} />}
        />
        <Route
          path="/mural"
          render={props => <Mural nivel={ni} userName={nameUser} />}
        />
        <Route
          path="/comunicados"
          render={props => <Comunicados nivel={ni} userName={nameUser} />}
        />
        <Route
          path="/galeriaAdm"
          render={props => <GaleriaAdm nivel={ni} userName={nameUser} />}
        />

        <Route path="/midle" component={Midleware} />
      </Switch>
    </HashRouter>
  );
};

ReactDOM.render(<Rotas />, document.getElementById("root"));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
