import React, { useEffect } from "react";
import "./style.css";
import Slider from "../Slider";
import imgBottonBorder from "../../../assets/playkids-2.png";
import imgCapa from "../../../assets/playkids-1.png";
import Header from "../../../components/Header";
import Footer from "../../../components/Footer";

const QuemSomos = () => {
  useEffect(() => {
    // encerra sessão
    // localStorage.removeItem("nivel");
  });
  return (
    <>
      <Header />
      <section id="quem-somos">
        {/*<Slider />*/}
        {/*<div className="container-title-quem-somos">
          <h2 className="white-text"> QUEM SOMOS </h2>
        </div>*/}
        <div className="container-capa" id="container-capa">
          <img src={imgCapa} className="responsive-img capa" alt="capa" />
        </div>
        <div className="infos-quem-somos">
          <h4 className="title-quem-somos">MISSÃO</h4>
          <p className="text-quem-somos">
            Oferecer um ensino de excelência desde os seus primeiros anos de
            vida, propiciando condições para uma aprendizagem significativa,
            formando pessoas atuantes em sociedade.
          </p>
          <h4 className="title-quem-somos">VISÃO</h4>
          <p className="text-quem-somos">
            Ser uma escola referencial em educação. Inovadora em suas propostas
            e práticas pedagógicas e na formação de cidadãos críticos.
          </p>
          <h4 className="title-quem-somos">PROPOSTA PEDAGÓGICA</h4>
          <p className="text-quem-somos proposta">
            Interacionismo; pois, acreditamos que o processo ensino-aprendizagem
            se dá na interação entre aluno e professor, aluno e aluno, e destes
            com o mundo. Compartilhamos com a ideia do psicólogo russo Lev
            Vygotsky que compreende ser humano como agente ativo no processo de
            aprendizagem, em que professor e aluno assumem papel ativo na
            construção do conhecimento
          </p>
        </div>
        <div>
          <img
            src={imgBottonBorder}
            className="responsive-img imgBotomBorder"
            alt="karatê"
          />
        </div>
      </section>
      <Footer />
    </>
  );
};

export default QuemSomos;
