import React, { useEffect } from "react";
import { BASE_URL } from "../../config/dev";
import { BASE_URL_FRONT } from "../../config/dev";

const Midleware = () => {
  useEffect(() => {
    let paramsHash = window.location.hash;
    let roughNivel = paramsHash.slice(0, 15);
    let idUser = paramsHash.slice(20, paramsHash.indexOf("&user"));
    let nameUser = paramsHash.slice(paramsHash.indexOf("&user=") + 6);
    // console.log("paramsHash");
    // console.log(paramsHash);
    // console.log("roughNivel");
    // console.log(roughNivel);
    // console.log("idUser");
    // console.log(idUser);
    // console.log("nameUser");
    // console.log(nameUser);
    if (roughNivel === "#/midle?nivel=0") {
      // user comum
      window.localStorage.setItem("nivel", 0);
      window.localStorage.setItem("cod", idUser);
      window.localStorage.setItem("user", nameUser);
      window.location.href = `${BASE_URL_FRONT}acesso`;
    } else if (roughNivel === "#/midle?nivel=1") {
      // user adm
      window.localStorage.setItem("nivel", 1);
      window.localStorage.setItem("cod", idUser);
      window.localStorage.setItem("user", nameUser);
      window.location.href = `${BASE_URL_FRONT}acesso`;
    } else {
      // erro (TRATAR E REDIRECIONAR)
    }
  });
  return (
    <div>
      <p>Redirecionando...</p>
    </div>
  );
};

export default Midleware;
