import React, { useState, useEffect } from "react";
import "./style.css";
import axios from "axios";
import InputMask from "react-input-mask";
import { BASE_URL } from "../../../config/dev";
import Header from "../../../components/HeaderAdm";

const Responsaveis = props => {
  const [cpf, setCpf] = useState("");
  const [senha, setSenha] = useState("");
  // const [nivel, setNivel] = useState("");

  useEffect(() => {
    // if (props.nivel != 1 && props.nivel != 0) {
    //   window.location.href = "/";
    // }
  });

  const getUser = () => {
    axios({
      method: "get",
      url: `${BASE_URL}/pais/getPais.php`
    })
      .then(function(response) {
        // console.log("resp get:");
        // console.log(response.data);
        let data = response.data;
        let str = data.split("next,");
        let json = [];
        // console.log(JSON.parse(response.data));
        // console.log(JSON.parse(str[0]));
        // console.log(JSON.parse(data));
        for (let i = 0; i < str.length; i++) {
          let registro = str[0].split(",");
          json.push({
            id: registro[0],
            cpf: registro[1],
            senha: registro[2],
            nome_resp1: registro[3],
            nome_resp2: registro[4],
            tel1: registro[5],
            tel2: registro[6],
            matricula_aluno: registro[7],
            nivel: registro[8]
          });
        }
        // console.log(json);
      })
      .catch(function(response) {
        // console.log(response);
      });
  };

  const cpfMask = value => {
    return value
      .replace(/\D/g, "") // substitui qualquer caracter que nao seja numero por nada
      .replace(/(\d{3})(\d)/, "$1.$2") // captura 2 grupos de numero o primeiro de 3 e o segundo de 1, apos capturar o primeiro grupo ele adiciona um ponto antes do segundo grupo de numero
      .replace(/(\d{3})(\d)/, "$1.$2")
      .replace(/(\d{3})(\d{1,2})/, "$1-$2")
      .replace(/(-\d{2})\d+?$/, "$1"); // captura 2 numeros seguidos de um traço e não deixa ser digitado mais nada
  };

  const validateForm = () => {
    let msgError = "";
    const resp1 = document.getElementById("resp1").value;
    const cpf = document.getElementById("cpf").value;
    const senha = document.getElementById("senha").value;
    const tel1 = document.getElementById("tel1").value;
    const matricula = document.getElementById("matricula").value;
    const nome_aluno = document.getElementById("nome_aluno").value;
    const ano_letivo = document.getElementById("ano_letivo").value;
    const turma = document.getElementById("turma").value;

    if (resp1 === "") {
      msgError += "Campo Responsável (1) é obrigatório!\n";
    }
    if (cpf === "") {
      msgError += "Campo CPF é obrigatório!\n";
    }
    if (senha === "") {
      msgError += "Campo Senha é obrigatório!\n";
    }
    if (tel1 === "") {
      msgError += "Campo Telefone (1) é obrigatório!\n";
    }
    if (matricula === "") {
      msgError += "Campo Matrícula Aluno é obrigatório!\n";
    }
    if (nome_aluno === "") {
      msgError += "Campo Nome Aluno é obrigatório!\n";
    }
    if (ano_letivo === "") {
      msgError += "Campo Ano Letivo é obrigatório!\n";
    }
    if (turma === "selecione") {
      msgError += "Campo Escolha a turma é obrigatório!\n";
    }

    return msgError;
  };

  const renderNivelAccess = () => {
    let retorno = null;
    if (props.nivel == 1) {
      retorno = (
        <>
          <Header nivel={props.nivel} userName={props.userName} />
          <section id="responsaveis-adm">
            <h2 className="margin-top-adm">Cadastro de Responsáveis</h2>
            {/*<form className="row">*/}
            <form
              className="row form-cadastro-resp"
              method="post"
              action={`${BASE_URL}pais/postPais.php`}
              onSubmit={e => {
                const validate = validateForm();
                if (validate !== "") {
                  alert(validate);
                  e.preventDefault();
                }
              }}
            >
              <div className="input-field col s12 m6">
                <input
                  id="resp1"
                  name="resp1"
                  type="text"
                  className="validate input-responsaveis  "
                  maxLength="60"
                />
                <label for="resp1">Responsável (1)</label>
              </div>
              <div className="input-field col s12 m6">
                <input
                  id="resp2"
                  name="resp2"
                  type="text"
                  className="validate  "
                  maxLength="60"
                />
                <label for="resp2">Responsável (2)</label>
              </div>
              <div className="input-field col s12 m6">
                <input
                  id="cpf"
                  name="cpf"
                  type="number"
                  className="validate  "
                  maxLength="14"
                />
                <label for="cpf">CPF</label>
              </div>
              <div className="input-field col s12 m6">
                <input
                  id="senha"
                  name="senha"
                  type="text"
                  className="validate  "
                  maxLength="20"
                />
                <label for="senha">Senha</label>
              </div>
              <div className="input-field col s12 m6">
                <input
                  id="tel1"
                  name="tel1"
                  type="text"
                  className="validate  "
                  maxLength="14"
                />
                <label for="tel1">Telefone (1)</label>
              </div>
              <div className="input-field col s12 m6">
                <input
                  id="tel2"
                  name="tel2"
                  type="text"
                  className="validate  "
                  maxLength="14"
                />
                <label for="tel2">Telefone (2)</label>
              </div>
              <div className="input-field col s12 m6">
                <input
                  id="matricula"
                  name="matricula"
                  type="text"
                  className="validate  "
                  maxLength="20"
                />
                <label for="matricula">Matrícula Aluno</label>
              </div>
              <div className="input-field col s12 m6">
                <input
                  id="nome_aluno"
                  name="nome_aluno"
                  type="text"
                  className="validate  "
                  maxLength="20"
                />
                <label for="nome_aluno">Nome Aluno</label>
              </div>
              <div className="input-field col s12 m6">
                <input
                  id="ano_letivo"
                  name="ano_letivo"
                  type="number"
                  className="validate  "
                />
                <label for="ano_letivo">Ano Letivo</label>
              </div>
              {/* <div className="input-field col s12 m6">
                <input
                  id="turma"
                  name="turma"
                  type="text"
                  className="validate  "
                />
                <label for="turma">Turma</label>
              </div> */}
              <div class="input-field col s12 m5">
                {/* <label for="turma">Escolha a turma: </label> */}
                <select
                  id="turma"
                  name="turma"
                  className="turma-select browser-default"
                >
                  <option value="selecione" selected>
                    Escolha a turma
                  </option>
                  <option value="Berçário">Berçário</option>
                  <option value="Mini Maternal">Mini Maternal</option>
                  <option value="Maternal">Maternal</option>
                  <option value="Infantil I">Infantil I</option>
                  <option value="Infantil II">Infantil II</option>
                  <option value="Infantil III">Infantil III</option>
                  <option value="1º ano">1º ano</option>
                  <option value="2º ano">2º ano</option>
                  <option value="3º ano">3º ano</option>
                  <option value="4º ano">4º ano</option>
                  <option value="5º ano">5º ano</option>
                </select>
                {/* <label>Turma</label> */}
              </div>

              {/* <div class="input-field col s12 m6">
                <select class="browser-default">
                  <option value="" disabled selected>
                    Choose your option 2
                  </option>
                  <option value="1">Option 1</option>
                  <option value="2">Option 2</option>
                  <option value="3">Option 3</option>
                </select>
              </div> */}

              {/* <input
          type="text"
          name="cpf"
          id="cpf"
          placeholder="CPF"
          onChange={e => setCpf(e.target.value)}
        />
        <input
          type="text"
          name="senha"
          id="senha"
          placeholder="Senha"
          onChange={e => setSenha(e.target.value)}
        /> */}

              <div className="col s12 center">
                <button
                  className="btn waves-effect waves-light center green"
                  type="submit"
                  name="action"
                >
                  SALVAR
                  <i className="material-icons right">send</i>
                </button>
              </div>
              {/* <button type="submit" onClick={e => postUser(e)}>
          ENVIAR
        </button> */}
              {/* <button type="button" onClick={e => postUser(e)}>
          ENVIAR
        </button> */}
            </form>
            <hr />
          </section>
        </>
      );
    } else {
      retorno = <div></div>;
    }
    return retorno;
  };

  return renderNivelAccess();
};

export default Responsaveis;
