import React, { useEffect } from "react";
import "./style.css";
import imgBallet from "../../../assets/ballet_new.png";
import imgIngles from "../../../assets/ingles.png";
import imgKarate from "../../../assets/karate.png";
import imgMusica from "../../../assets/musica.png";
import imgBorder from "../../../assets/playkids-3.png";
import Header from "../../../components/Header";
import Footer from "../../../components/Footer";

const Atividades = () => {
  useEffect(() => {
    // encerra sessão
    // localStorage.removeItem("nivel");
  });
  return (
    <>
      <Header />
      <section id="atividades">
        <div className="sub-container-atividades">
          <div>
            <h2>ATIVIDADES EXTRACURRICULARES</h2>
          </div>
          <div className="container-atividade ballet-container" id="ballet">
            <div>
              <img
                src={imgBallet}
                className="img-atividades-ballet"
                alt="ballet"
              />
              {/*<img src={imgBallet} className="responsive-img" alt="ballet" />*/}
            </div>
            <div className="title-description-activity">
              <h4 className="ballet-title">BALLET</h4>
              <p>
                O ballet combina atividades físicas com musicalidade,
                lateralidade, coordenação motora e um pouco de teatralização.
                Auxilia na concentração, postura e ritmo.
              </p>
            </div>
          </div>
          <div className="container-atividade ingles-container" id="ingles">
            <div>
              <img
                src={imgIngles}
                className="img-atividades-ingles"
                alt="ingles"
              />
            </div>
            <div className="title-description-activity">
              <h4 className="ingles-title">INGLÊS</h4>
              <p>
                Estudar um segundo idioma na infância estimula as funções
                cognitivas, sendo extremamente positivo para o aproveitamento
                das outras diciplinas.
              </p>
            </div>
          </div>
          <div className="container-atividade karate-container" id="judo">
            <div>
              <img
                src={imgKarate}
                className="img-atividades-karate"
                alt="karatê"
              />
            </div>
            <div className="title-description-activity">
              <h4 className="karate-title">
                KARATÊ
                {/* <span class="new badge red" data-badge-caption="Em Breve!"></span> */}
              </h4>
              <p>
                Por meio da prática do karatê, as crianças podem experimentar
                movimentos inéditos e diversificados, melhoram a coordenação
                motora e o domínio corporal.
              </p>
            </div>
          </div>
          <div className="container-atividade musica-container" id="musica">
            <div>
              <img
                src={imgMusica}
                className="img-atividades-musica"
                alt="música"
              />
            </div>
            <div className="title-description-activity-music">
              <h4 className="musica-title">MÚSICA</h4>
              <p>
                Ajuda a melhorar a sensibilidade das crianças, a capacidade de
                concentração e a memória, trazendo benefícios ao processo de
                alfabetização e ao raciocínio matemático.
              </p>
            </div>
          </div>
        </div>
        <img
          src={imgBorder}
          className="img-border-atividades responsive-img"
          alt="border image"
        />
      </section>
      <Footer />
    </>
  );
};

export default Atividades;
