import React, { Component } from "react";
import M from "materialize-css";
import "./style.css";
import "materialize-css/dist/css/materialize.min.css";
import { BASE_URL } from "../../config/dev";

class inputEditResp extends Component {
  constructor(props) {
    super(props);
  }

  componentDidMount() {
    console.log("new props component input");
    console.log(this.props);
  }

  edit(e, id, numInput) {
    document.getElementById(
      "btn_edit_" + this.props.campo + "_" + id
    ).style.display = "none";
    document.getElementById(
      "btn_confirm_edit_" + this.props.campo + "_" + id
    ).style.display = "block";
    document.getElementById(`${this.props.campo}_hidden_${id}`).style.display =
      "block";
    // switch (numInput) {
    //   case 1:
    //     document.getElementById(`resp1_hidden_${id}`).style.display = "block";
    // }
  }

  renderInput() {
    let retorno = null;
    if (this.props.select === 1) {
      retorno = (
        <select
          id={this.props.campo + "_hidden_" + this.props.id}
          name={this.props.campo + "_hidden_" + this.props.id}
          className=" turma-select browser-default elem-display-none"
        >
          {/* <option value="selecione" selected>
            Escolha a turma
          </option> */}
          <option value="Berçário">Berçário</option>
          <option value="Mini Maternal">Mini Maternal</option>
          <option value="Maternal">Maternal</option>
          <option value="Infantil I">Infantil I</option>
          <option value="Infantil II">Infantil II</option>
          <option value="Infantil III">Infantil III</option>
          <option value="1º ano">1º ano</option>
          <option value="2º ano">2º ano</option>
          <option value="3º ano">3º ano</option>
          <option value="4º ano">4º ano</option>
          <option value="5º ano">5º ano</option>
        </select>
      );
    } else {
      retorno = (
        <input
          id={this.props.campo + "_hidden_" + this.props.id}
          name={this.props.campo + "_hidden_" + this.props.id}
          type="text"
          className="elem-display-none"
          maxLength="60"
          placeholder={`Inserir novo ${this.props.label}`}
        />
      );
    }
    return retorno;
  }

  render() {
    return (
      <>
        <form
          id={`form_edit_${this.props.id}`}
          className="row flex-center"
          method="post"
          action={`${BASE_URL}pais/updatePais.php`}
          onSubmit={e => {
            if (window.confirm("Tem certeza que deseja alterar esse campo?")) {
              // console.log("editado!");
            } else {
              e.preventDefault();
              document.getElementById(
                `${this.props.campo}_hidden_${this.props.id}`
              ).style.display = "none";
              document.getElementById(
                "btn_edit_" + this.props.campo + "_" + this.props.id
              ).style.display = "block";
              document.getElementById(
                "btn_confirm_edit_" + this.props.campo + "_" + this.props.id
              ).style.display = "none";
            }
          }}
        >
          <div className="col s8">
            <div className="flex-center">
              <b>{this.props.label}:</b>

              {/* talvez padronizar nome do id e name */}
              <input
                id={"resp1_" + this.props.id}
                name={"resp1_" + this.props.id}
                type="text"
                className="input-edit"
                disabled
                value={this.props.data}
                // onChange={e => editHidden(e, i, valuesHidden[i].nome_resp1)}
              />
            </div>
            {this.renderInput()}
          </div>
          {/* talvez não enviar id nos dois input */}
          <input type="hidden" name="id_registro" value={this.props.id} />
          <input type="hidden" name="id_form" value={this.props.id} />
          <input type="hidden" name="type_campo" value={this.props.campo} />
          <div className="col s4">
            <div id={`btn_edit_${this.props.campo}_${this.props.id}`}>
              <button
                className="waves-effect waves-light btn btn-edit blue"
                onClick={e => this.edit(e, this.props.id, 1)}
                type="button"
              >
                <i class="material-icons">edit</i>
              </button>
            </div>
            <div
              className="confirm-edit"
              id={`btn_confirm_edit_${this.props.campo}_${this.props.id}`}
            >
              <button
                className="waves-effect waves-light btn btn-edit green"
                // onClick={e =>
                //   save(e, this.props..id, this.props..id)
                // }
              >
                <i class="material-icons">done</i>
              </button>
            </div>
          </div>
        </form>
      </>
    );
  }
}

export default inputEditResp;
