import React, { useState, useEffect } from "react";
import "./style.css";
import axios from "axios";
import { BASE_URL } from "../../../config/dev";
import Header from "../../../components/HeaderAdm";

const Galeria = props => {
  const [listImg, setListImg] = useState([]);
  const [attImg, setAttImg] = useState(false);

  useEffect(() => {
    getImg();
  }, [attImg]);

  const getImg = () => {
    axios({
      method: "get",
      url: `${BASE_URL}galeria/getImg.php`
    })
      .then(function(response) {
        let data = response.data;
        let str = data.split("next,");
        let json = [];
        for (let i = 0; i < str.length - 1; i++) {
          let registro = str[i].split(",");
          json.push({
            id: registro[0],
            diretorio: registro[1].slice(2, registro[1].length)
          });
        }
        setListImg(json);
        setAttImg(true);
      })
      .catch(function(response) {});
  };

  const renderNivelAccess = () => {
    let retorno = null;
    if (props.nivel == 1) {
      retorno = (
        <section id="galeria-adm">
          <Header nivel={props.nivel} userName={props.userName} />
          <h2 className="margin-top-adm black-text">Galeria</h2>
          <div>
            <form
              enctype="multipart/form-data"
              action={`${BASE_URL}galeria/postImg.php`}
              method="POST"
              className="form-cadastro-img-galeria row"
            >
              <div class="file-field input-field col s12 m8">
                <div class="btn orange">
                  <span>NOVA IMAGEM</span>
                  <input type="file" className="" name="file_img" />
                </div>
                <div class="file-path-wrapper">
                  <input
                    className="file-path validate input-file"
                    type="text"
                    placeholder="SELECIONE..."
                  />
                </div>
              </div>
              <div className="center col s12 m4">
                <input
                  type="submit"
                  value="Enviar arquivo"
                  className="btn btn-success green"
                />
              </div>
            </form>
          </div>

          <div className="container-view-galeria">
            <div className="row container container-galeria-macro">
              {imgGaleria()}
            </div>
          </div>
        </section>
      );
    } else {
      retorno = <div></div>;
    }
    return retorno;
  };

  const imgGaleria = () => {
    let rows = [];
    for (let i = 0; i < listImg.length; i++) {
      rows.push(
        <div className="col s4 container-img-galeria">
          <a
            href={`${BASE_URL}/galeria/${listImg[i].diretorio}`}
            target="_blank"
          >
            <img
              src={`${BASE_URL}/galeria/${listImg[i].diretorio}`}
              className="img-galeria-responsive"
              alt={`galeria_img_${listImg[i].id}`}
            />
          </a>
          <div className="center">
            <form
              id={`form_delete_${i}`}
              className="row"
              method="post"
              action={`${BASE_URL}galeria/deleteImg.php`}
              onSubmit={e => {
                if (
                  window.confirm(
                    "Tem certeza que deseja excluir a imagem acima?"
                  )
                ) {
                  // console.log("excluido!");
                } else {
                  e.preventDefault();
                }
              }}
            >
              <input type="hidden" name="id_img" value={listImg[i].id} />
              <input
                type="hidden"
                name="dir_img"
                value={listImg[i].diretorio}
              />
              <button
                type="submit"
                className="waves-effect waves-light btn btn-delete red delete-img"
              >
                <i class="material-icons">delete</i>
              </button>
            </form>
          </div>
        </div>
      );
    }
    return rows;
  };

  return renderNivelAccess();
};

export default Galeria;
