import React, { Component } from "react";
import M from "materialize-css/dist/js/materialize.min.js";
import "./style.css";
import imgNutrico from "../../../assets/parcerias/nutrico.png";
import imgChack from "../../../assets/parcerias/chack.png";
import imgFtd from "../../../assets/parcerias/ftd.png";
import Header from "../../../components/Header";
import Footer from "../../../components/Footer";

export default class Parcerias extends Component {
  componentDidMount() {
    // localStorage.removeItem("nivel");
    var elem = document.querySelector(".slides2");
    var instance = M.Carousel.init(elem, {});
    // var instance = M.Carousel.init(elem, { duration: 200 });
  }
  render() {
    return (
      <>
        <Header />
        <div className="center-align container-parceiras" id="parcerias">
          <h2 className="white-text">PARCEIRAS</h2>
          <div className="carousel slides2">
            <a className="carousel-item carousel-item-parceiras" href="#a!">
              {/* <h5>slide 1</h5> */}
              <img
                src={imgNutrico}
                className="responsive-img img-parceiras"
                alt="parceiras"
              />
              <p className="black-text">
                A Nutrico está desde 1995 atuando na área de alimentação
                infantil para o segmento de escolas, oferecendo uma alimentação
                saudável, saborosa e de qualidade
              </p>
            </a>
            <a className="carousel-item carousel-item-parceiras" href="#b!">
              {/* <h5>slide 2</h5> */}
              <img
                src={imgChack}
                className="responsive-img img-parceiras"
                alt="parceiras"
              />
              <p className="black-text">
                A Chack Uniformes é especializada no atendimento a escolas em
                todo o Brasil. Com o foco na satisfação dos nossos clientes
                produzimos uniformes de qualidade para alunos, professores e
                funcionários.
              </p>
            </a>
            <a className="carousel-item carousel-item-parceiras" href="#c!">
              {/* <h5>slide 3</h5> */}
              <img
                src={imgFtd}
                className="responsive-img img-parceiras"
                alt="parceiras"
              />
              <p className="black-text">
                Nossos materiais estão estruturados em princípios educacionais,
                que tomam como ponto de partida os conhecimentos prévios do
                aluno e abordam os conteúdos de forma contextualizada de modo a
                estabelecer um diálogo entre aluno e professor.
              </p>
            </a>
          </div>
        </div>
        <Footer />
      </>
    );
  }
}
